* {
  margin: 0;
  text-align: center;
	box-sizing: border-box; /*include padding and margin measurements*/
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

html {
  background: linear-gradient(#ffbb00, purple);
  height: -webkit-fill-available;
  height: 100vh;
}
 

div {
  font-style: oblique;
  color: purple;
}

h1 {
  margin-bottom: 0.4em;
  font-size: 70px;
  padding-top: 5%;
  
}

button {
  background-color: #e9ab02;
  font-size: 15px;
  font-weight: bolder;
  color: purple;
  padding:15px;
  border-radius: 10px;
  border: 2px purple outset;
}

button:hover {
  color: #e9ab02 ;
  background-color: purple;
  cursor: pointer;
} 

img {
  width: 300px;
  height: 300px;
}